.hotelHome {
    position: relative;
    width: 100%;
    max-width: 275px;
    margin: auto;
   
    border-radius: 8px;
    overflow: hidden;
    height: 100%;
    text-decoration: none;
  
    
  }
  * {
    box-sizing: border-box;
  }
  

.hotel__favourite {
    position: absolute;
    width: 24px;
    height: 24px;
      display: flex;
      transition: all 0.5s;
      border-radius: 50%;
      justify-content: center;
      align-items: center;
      z-index: 100;
    
      border: none;
      top: 16px;
      left: 16px;
    
      background-color: #000;
      opacity: 0.7;
    }
    
    .hotel__favourite:hover {
      opacity: 0.8;
    }
    
    .hotel__favourite:active {
      opacity: 0.9;
    }
    
    .hotel__favourite--active {
      background-color: #fff;
    }
    
    
    .hotel__favourite-starbest {
      display: block;
      width: 12px;
      opacity: 1; 
      z-index: 101;
      height: 12px;
      
    }
    
    .hotel__slider {
      position: relative;
    
      overflow: hidden;
    
      height: 275px;
      width: 275px;
    
    }


    
.section
{
  height: 275px;
  width: 275px;
  text-decoration: none;
}

.section-center {
  display: flex;
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden; 
}

.section article {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out; 
  opacity: 0; 
  pointer-events: none;
}

.section article.activeSlide {
  opacity: 1; 
  pointer-events: auto; 
  transform: translateX(0); 
}

.section article.nextSlide {
  transform: translateX(100%);
}

.section article.lastSlide {
  transform: translateX(-100%);
}


.prev,
.next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent; 
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
  color: black; 
  font-size: 24px; 
  padding: 10px; 
}

.prev {
  left: 10px;
}

.next {
  right: 10px;
}





.carousel-indicators {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
}

.indicator {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: #ccc;
  border-radius: 50%;
  cursor: pointer;
  margin: 0 5px;
  transition: background-color 0.3s ease;
}

.indicator.active {
  background-color: #333;
}


.hotel__image {
  display: block;
  width: 100%;
  height: auto;
  z-index: 50;
}






.hotel__title {
  margin-top: 15px;
  margin-right: 70px;
  font-size: 16px;
  font-weight: 600;
  line-height: 21.82px;
  color: rgb(34, 34, 34);
}

.hotel__stars {
  display: flex;
}

.hotel__stars-star {
  width: 16px;
  height: 16px;
  display: block;
  background-image: url('/public/Home/HotelList/hotel__star.png');
  background-position: center;
  background-size: 11.5px 11px;
  background-repeat: no-repeat;
}

.hotel__distance {
  margin-top: 8px;
  margin-bottom: 13px;
  font-size: 16px;
  font-weight: 400;
  line-height: 21.82px;
  text-align: left;
  color: rgb(113, 113, 113);
}

.hotel__cost {
  font-size: 16px;
  font-weight: 800;
  line-height: 21.82px;
  text-align: left;
  color: rgb(34, 34, 34);
}

